
.ganbi{
  display: block;
  float: right;
  margin-top: -40px;
  margin-right: 10px;
  font-size: 30px;
  color: #3333;
}
.ganbi:before{
  clear: both;
}
.js-modal-mask {
  position: fixed;
  background-color: #000;
  bottom: 0px;
  font-size: 14px;
  height: 100%;
  left: 0px;
  opacity: 0.2;
  filter: alpha(opacity=20);
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(opacity=20)";
  right: 0px;
  top: 0px;
  z-index: 11030;
  display: none;
}
.js-modal-mask {

  -webkit-box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);

          box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
}
.box{
  position: fixed;
  z-index: 11031;
  width:390px;
  height:320px;
  border:solid 1px #ddd;
  background:#FFF;
  left:50%;
  top:42%;
  margin-left:-195px;
  margin-top:-130px;
  display: none;
}
.js_show {
  display: block;
}
.box h2{
  font-weight:normal;
  color:#666;
  font-size:16px;
  line-height:46px;
  height:46px;
  overflow:hidden;
  text-align:center;
  border-bottom:solid 1px #ddd;
  background:#f7f7f7;
}
.input_box{
  width:350px;
  padding-bottom:15px;
  margin:0 auto;
  overflow:hidden;
}
.input_box input{
  float:left;
  width:348px;
  height:40px;
  font-size:14px;
  border:solid 1px #ddd;
  text-indent:10px;
  outline:none;
  line-height:40px;
}
.input_box button{
  width:350px;
  height:48px;
  background:#3f89ec;
  border:none;
  border-radius:2px;
  outline:none;
  cursor:pointer;
  font-size:16px;
  color:#FFF;
}
#error_box{
  height:40px;
  width:350px;
  margin:0 auto;
  line-height:40px;
  color:#fc4343;
}



